import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { formatDateTime } from "@/lib/utils/emi-calculator";
import { UserActionsDropdown } from "@/components/admin/user-actions-dropdown";
import { Users, Shield, Clock, CheckCircle2, XCircle } from "lucide-react";
import { Profile, Account } from "@/lib/models";
import dbConnect from "@/lib/mongodb";

interface PageProps {
  searchParams: Promise<{ page?: string }>;
}

export default async function AdminUsersPage(props: PageProps) {
  await dbConnect();

  const searchParams = await props.searchParams;
  const currentPage = parseInt(searchParams.page || "1");
  const itemsPerPage = 20;
  const skip = (currentPage - 1) * itemsPerPage;

  // Get all accounts to find all users
  const allAccounts = await Account.find({}).lean();
  const uniqueUserIds = [...new Set(allAccounts.map((acc: any) => acc.userId))];

  // Fetch profiles for users who have them
  const profiles = await Profile.find({}).lean();
  const profilesMap = new Map(profiles.map((p: any) => [p.userId, p]));

  // Build user list from accounts and profiles
  const allUsers = uniqueUserIds.map((userId) => {
    const account = allAccounts.find((acc: any) => acc.userId === userId);
    const profile = profilesMap.get(userId);

    // Check if profile is complete (has address information)
    const isVerified =
      profile?.address?.street &&
      profile?.address?.city &&
      profile?.address?.state &&
      profile?.address?.zipCode;

    return {
      _id: profile?._id || account?._id,
      userId: userId,
      fullName: profile?.fullName || account?.userId || "Unknown User",
      email: profile?.email || "No email",
      phone: profile?.phone || "-",
      isVerified: isVerified || false,
      isAdmin: profile?.isAdmin || false,
      createdAt: profile?.createdAt || account?.createdAt || new Date(),
      hasProfile: !!profile,
      address: profile?.address,
    };
  });

  // Sort by creation date
  allUsers.sort(
    (a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime()
  );

  // Pagination
  const totalUsers = allUsers.length;
  const paginatedUsers = allUsers.slice(skip, skip + itemsPerPage);
  const totalPages = Math.ceil(totalUsers / itemsPerPage);

  // Count stats
  const verifiedUsers = allUsers.filter((u) => u.isVerified).length;
  const unverifiedUsers = allUsers.filter((u) => !u.isVerified).length;
  const adminUsers = allUsers.filter((u) => u.isAdmin).length;

  const getVerificationBadge = (isVerified: boolean) => {
    if (isVerified) {
      return (
        <Badge className="bg-green-500 hover:bg-green-600 text-white gap-1">
          <CheckCircle2 className="h-3 w-3" />
          Verified
        </Badge>
      );
    }
    return (
      <Badge variant="secondary" className="gap-1">
        <Clock className="h-3 w-3" />
        Unverified
      </Badge>
    );
  };

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-2xl font-bold text-foreground">User Management</h1>
        <p className="text-muted-foreground">
          Manage users and profile verifications
        </p>
      </div>

      {/* Stats */}
      <div className="grid gap-4 sm:grid-cols-4">
        <Card className="border-0 shadow-sm">
          <CardContent className="p-4">
            <div className="flex items-center gap-3">
              <div className="flex h-10 w-10 shrink-0 items-center justify-center rounded-lg bg-primary/10">
                <Users className="h-5 w-5 text-primary" />
              </div>
              <div className="min-w-0">
                <p className="text-sm text-muted-foreground">Total Users</p>
                <p className="text-xl font-semibold text-primary">
                  {totalUsers}
                </p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="border-0 shadow-sm">
          <CardContent className="p-4">
            <div className="flex items-center gap-3">
              <div className="flex h-10 w-10 shrink-0 items-center justify-center rounded-lg bg-yellow-500/10">
                <Clock className="h-5 w-5 text-yellow-600" />
              </div>
              <div className="min-w-0">
                <p className="text-sm text-muted-foreground">Unverified</p>
                <p className="text-xl font-semibold text-yellow-600">
                  {unverifiedUsers}
                </p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="border-0 shadow-sm">
          <CardContent className="p-4">
            <div className="flex items-center gap-3">
              <div className="flex h-10 w-10 shrink-0 items-center justify-center rounded-lg bg-green-500/10">
                <CheckCircle2 className="h-5 w-5 text-green-600" />
              </div>
              <div className="min-w-0">
                <p className="text-sm text-muted-foreground">Verified</p>
                <p className="text-xl font-semibold text-green-600">
                  {verifiedUsers}
                </p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="border-0 shadow-sm">
          <CardContent className="p-4">
            <div className="flex items-center gap-3">
              <div className="flex h-10 w-10 shrink-0 items-center justify-center rounded-lg bg-red-500/10">
                <Shield className="h-5 w-5 text-red-600" />
              </div>
              <div className="min-w-0">
                <p className="text-sm text-muted-foreground">Admins</p>
                <p className="text-xl font-semibold text-red-600">
                  {adminUsers}
                </p>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Users Table */}
      <Card className="border-0 shadow-sm">
        <CardHeader className="flex flex-row items-center justify-between">
          <CardTitle>All Users</CardTitle>
          <div className="text-sm text-muted-foreground">
            Showing {skip + 1}-{Math.min(skip + itemsPerPage, totalUsers)} of{" "}
            {totalUsers}
          </div>
        </CardHeader>
        <CardContent className="p-0">
          <div className="w-full">
            <div className="max-w-full overflow-x-auto">
              <table className="w-full">
                <thead>
                  <tr className="border-b border-border">
                    <th className="px-4 py-3 text-left text-xs font-medium text-muted-foreground whitespace-nowrap">
                      User
                    </th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-muted-foreground whitespace-nowrap">
                      Phone
                    </th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-muted-foreground whitespace-nowrap">
                      Profile Status
                    </th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-muted-foreground whitespace-nowrap">
                      Role
                    </th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-muted-foreground whitespace-nowrap">
                      Joined
                    </th>
                    <th className="px-4 py-3 text-right text-xs font-medium text-muted-foreground whitespace-nowrap">
                      Actions
                    </th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-border">
                  {paginatedUsers?.map((user: any) => (
                    <tr key={user._id.toString()} className="hover:bg-muted/50">
                      <td className="px-4 py-3">
                        <div className="flex items-center gap-2 min-w-[200px]">
                          <div className="flex h-10 w-10 shrink-0 items-center justify-center rounded-full bg-primary/10 text-sm font-semibold text-primary">
                            {user.fullName?.charAt(0)?.toUpperCase() || "U"}
                          </div>
                          <div className="min-w-0">
                            <p className="font-medium text-sm truncate">
                              {user.fullName}
                            </p>
                            <p className="text-xs text-muted-foreground truncate">
                              {user.email}
                            </p>
                          </div>
                        </div>
                      </td>
                      <td className="px-4 py-3 text-sm whitespace-nowrap">
                        {user.phone}
                      </td>
                      <td className="px-4 py-3">
                        {getVerificationBadge(user.isVerified)}
                      </td>
                      <td className="px-4 py-3">
                        {user.isAdmin ? (
                          <Badge
                            variant="destructive"
                            className="whitespace-nowrap"
                          >
                            Admin
                          </Badge>
                        ) : (
                          <Badge
                            variant="outline"
                            className="whitespace-nowrap"
                          >
                            User
                          </Badge>
                        )}
                      </td>
                      <td className="px-4 py-3 text-xs text-muted-foreground whitespace-nowrap">
                        {formatDateTime(new Date(user.createdAt))}
                      </td>
                      <td className="px-4 py-3 text-right">
                        <UserActionsDropdown user={user} />
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>

          {/* Pagination */}
          {totalPages > 1 && (
            <div className="px-6 py-4 border-t border-border flex items-center justify-between">
              <div className="text-sm text-muted-foreground">
                Page {currentPage} of {totalPages}
              </div>
              <div className="flex gap-2">
                {currentPage > 1 && (
                  <a href={`/admin/users?page=${currentPage - 1}`}>
                    <button className="px-3 py-1 text-sm border rounded hover:bg-muted">
                      Previous
                    </button>
                  </a>
                )}
                {currentPage < totalPages && (
                  <a href={`/admin/users?page=${currentPage + 1}`}>
                    <button className="px-3 py-1 text-sm border rounded hover:bg-muted">
                      Next
                    </button>
                  </a>
                )}
              </div>
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  );
}
